import SearchBox from 'base/search-box/search-box'
import SearchList from 'base/search-list/search-list'
import { mapActions, mapGetters } from 'vuex'
import { getHotsearch } from 'api/api'
export default {
  data() {
    return {
      hotKey: [],
      query: '',
    }
  },
  computed: {
    // shortcut() {
    //   return this.hotKey.concat(this.searchHistory)
    // }
  },
  created() {
    this._getHotKey()
  },
  computed: {
    ...mapGetters([
      'searchHistory'
    ])
  },
  methods: {
    _getHotKey() {
      getHotsearch().then((res) => {
        this.hotKey = res.data
      })
    },
    onQueryChange(query) {
      // console.log(query)
      // this.query = query
    },
    enter(query) {
      // console.log(query)
      this.saveSearchHistory(query)
    },
    saveSearch() {
      
    },
    ...mapActions([
      'saveSearchHistory'
    ])
  },
  watch: {

  },
  components: {
    SearchBox,
    SearchList
  }
}



