import {debounce} from 'common/js/util'

export default {
  props: {
    placeholder: {
      type: String,
      default: '农丁原浆'
    }
  },
  data() {
    return {
      query: ''
    }
  },
  methods: {
    clear() {
      this.query = ''
    },
    cancel() {
      this.$router.back()
    },
    setQuery(query) {
      this.query = query
    },
    blur() {
      this.$refs.query.blur()
    },
    enter() {
      this.$emit('enter', this.query)
    }
  },
  created() {
    this.$watch('query', debounce((newQuery) => {
      this.$emit('query', newQuery)
    }, 200))
  }
}



